import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import withRoot from '../withRoot';

import Typography from 'material-ui/Typography';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';

import FacebookIcon from '../../static/svgcomp/Facebook';
import GithubIcon from '../../static/svgcomp/Github';
import InstagramIcon from '../../static/svgcomp/Instagram';

const styles = theme => ({
  root: {
    backgroundImage: 'linear-gradient(to top, #48c6ef 0%, #6f86d6 100%)',
    height: '100vh',
    boxSizing: 'unset',
  },
  appBar: {
    margin: '0 auto',
    paddingTop: 25,
    display: 'flex',
    maxWidth: 1000,
    [theme.breakpoints.down('md')]: {
      maxWidth: 800,
      padding: '20px 20px 0px 0px',
    },
  },
  otherMenu: {
    marginLeft: 50,
    [theme.breakpoints.down('md')]: {
      marginLeft: 30,
    },
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  bodyContent: {
    maxWidth: 1000,
    margin: '0 auto',
    display: 'flex',
    [theme.breakpoints.down('md')]: {
      maxWidth: 800,
    },
    [theme.breakpoints.down('sm')]: {
      padding: '0px 20px',
    },
    '& > div': {
      width: '65%',
    },
    '@media (max-width: 670px)': {
      '& > div': {
        width: '100%',
      },
    },
  },
  people: {
    width: '35%',
    '@media (max-width: 670px)': {
      display: 'none',
    },
  },
  title: {
    color: '#FFF',
    fontWeight: 400,
    textTransform: 'unset',
    borderRadius: 3,
  },
  divContent: {
    marginTop: 100,
    marginBottom: 30,
    '@media (max-width: 670px)': {
      marginTop: 40,
    },
  },
  white: {
    color: '#FFF',
  },
  card: {
    width: 145,
    position: 'relative',
    height: 200,
    marginRight: 10,
    borderRadius: 5,
  },
  iconCard: {
    width: 35,
    height: 35,
    padding: 15,
    borderRadius: 100,
    margin: '20px auto 15px',
    background: 'rgba(0,0,0, 0.25)',
  },
  iconHover: {
    color: '#FFF',
    '&:hover': {
      fill: 'rgba(255, 255, 255, 0.5)',
    },
  },
  typoContent: {
    bottom: 0,
    margin: '20px 8px',
    color: '#FFF',
    position: 'absolute',
  },
});

const kotak = [
  {
    bg: '#52C9F5',
    content: 'Shalah means Prayer worsiping God',
    title: 'Shalah',
    icon: 'icons/Ruku.svg',
  },
  {
    bg: '#F7A79C',
    content: 'Du\'a is a direct request to God for Goodness',
    title: 'Du\'a',
    icon: 'icons/Mosque.svg',
  },
  {
    bg: '#939AE1',
    content: 'Qur\'an is the holy book of islam religion',
    title: 'Qur\'an',
    icon: 'icons/Quran.svg',
  },
];

class Index extends React.Component {

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Helmet defaultTitle="adnanfajlur">
          <title>adnanfajlur</title>
          <html lang="en" />
        </Helmet>
        <div className={classes.root}>
          <div className={classes.appBar}>
            <Button className={classes.title}>Home</Button>
            <div className={classes.otherMenu}>
              <Button className={classes.title}>About Me</Button>
              <Button className={classes.title}>Contact Me</Button>
            </div>
            <div style={{ marginLeft: 'auto', display: 'flex', alignItems: 'center' }}>
              <GithubIcon className={classes.iconHover} />
              <InstagramIcon className={classes.iconHover} />
              <FacebookIcon className={classes.iconHover} />
            </div>
          </div>
          <div className={classes.bodyContent}>
            <div>
              <div className={classes.divContent}>
                <Typography variant="display1" className={classes.white}>Hi there, I'm</Typography>
                <Typography variant="headline" className={classes.white}>Adnan Fajlur Rohman</Typography>
                <div style={{ marginTop: 40 }}>
                  <Typography className={classes.white}>A man born in 1999</Typography>
                  <Typography className={classes.white}>in a small village in the city of Rembang, Indonesia</Typography>
                  <Typography className={classes.white}>my passion is in programmers, especially front-end</Typography>
                </div>
              </div>
              <div style={{ display: 'flex' }}>
                {kotak.slice(0, 2).map((n, i) => (
                  <div
                    key={i}
                    className={classes.card}
                    style={{ backgroundColor: n.bg }}
                  >
                    <div className={classes.iconCard}>
                      <img src={n.icon} alt="" />
                    </div>
                    <Typography className={classes.white} align="center">{n.title}</Typography>
                    <Typography variant="caption" className={classes.typoContent} align="center" >{n.content}</Typography>
                  </div>
                ))}
              </div>
            </div>
            <div className={classes.people}>
              <div style={{ marginTop: 50, float: 'right' }}>
                <img src="icons/People.svg" alt="" style={{ width: 300, height: 488 }} />
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

Index.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(Index));



